.App {
  font-family: sans-serif;
  text-align: center;
}

body {
  background: #222;
  font-family: "Comfortaa", sans-serif;
}

aside.context {
  text-align: center;
  color: #fff;
  line-height: 1.7;
}
aside.context a {
  text-decoration: none;
  color: #fff;
