}

.main-content {
  text-align: center;
  margin: 4em auto;
}

.map-base {
  width: 306px;
  height: 600px;
  margin: auto;
  background: url("https://rawcdn.githack.com/aakashapoorv/Marauder-Map-in-React/38904ba2bd14605682c10e56bd306597fb334c22/public/img/9.png") center center/cover;
  position: relative;
  display: inline-block;
}

.map-flap {
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  position: absolute;
  width: 100%;
  height: 25%;
  margin: auto;
  left: 0;
  right: 0;
  transition: 0.5s ease;
  top: 25%;
}
.map-flap__front,
.map-flap__back {
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  width: 100%;
  height: 100%;
  position: absolute;
}
.map-flap__back {
  -webkit-transform: scale(-1) rotateY(180deg);
          transform: scale(-1) rotateY(180deg);
}
.map-flap.flap--1 {
  box-shadow: 0 -1px 6px rgba(97, 83, 73, 0.5);
}
.map-flap.flap--1 .map-flap__front {
  background: url("https://rawcdn.githack.com/aakashapoorv/Marauder-Map-in-React/38904ba2bd14605682c10e56bd306597fb334c22/public/img/mini-1.png") center left/cover;
}
.map-flap.flap--1 .map-flap__back {
  background: url("https://rawcdn.githack.com/aakashapoorv/Marauder-Map-in-React/38904ba2bd14605682c10e56bd306597fb334c22/public/img/mini-3.png") -3px 0 / cover;
}
.map-flap.flap--2 {
  box-shadow: 0 1px 6px rgba(97, 83, 73, 0.5);
  top: 50%;
}
.map-flap.flap--2 .map-flap__front {
  background: url("https://rawcdn.githack.com/aakashapoorv/Marauder-Map-in-React/38904ba2bd14605682c10e56bd306597fb334c22/public/img/mini-2.png") center left/cover;
}
.map-flap.flap--2 .map-flap__back {
  background: url("https://rawcdn.githack.com/aakashapoorv/Marauder-Map-in-React/38904ba2bd14605682c10e56bd306597fb334c22/public/img/mini-4.png") -3px 0 / cover;
}

.map-side {
  height: 600px;
  width: 152px;
  position: absolute;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  transition: 0.3s ease;
}
.map-side .front,
.map-side .back {
  width: 100%;
  height: 100%;
  position: absolute;
  background-repeat: no-repeat;
  background-position: left top;
  background-size: cover;
  background-image: var(--image);
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
}
.map-side .back {
  background-image: url(https://rawcdn.githack.com/aakashapoorv/Marauder-Map-in-React/38904ba2bd14605682c10e56bd306597fb334c22/public/img/back.png);
}

.map-side {
  top: 0;
}
.map-side.side-1 {
  left: 0;
  margin-left: 1.5px;
}
.map-side.side-2 {
  left: 50%;
  margin-left: -2px;
}
.map-side.side-3 {
  left: 0;
  margin-left: 3px;
}
.map-side.side-3 .back {
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}
.map-side.side-4 {
  left: 50%;
  margin-left: -1px;
}
.map-side.side-4 .back {
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}
.map-side.side-5 {
  left: 0;
}
.map-side.side-5 .back {
  background-image: url(https://rawcdn.githack.com/aakashapoorv/Marauder-Map-in-React/38904ba2bd14605682c10e56bd306597fb334c22/public/img/1.png);
}
.map-side.side-6 {
  left: 50%;
}
.map-side.side-6 .front {
  background-size: 99.5%;
}
.map-side.side-6 .back {
  background-image: url(https://rawcdn.githack.com/aakashapoorv/Marauder-Map-in-React/38904ba2bd14605682c10e56bd306597fb334c22/public/img/17.png);
}

.scroll-name {
  position: absolute;
  bottom: 105px;
  left: -60px;
  width: 150px;
  height: 30px;
  font: 15px Satisfy, cursive;
  text-align: center;
  background: url("https://rawcdn.githack.com/aakashapoorv/Marauder-Map-in-React/38904ba2bd14605682c10e56bd306597fb334c22/public/img/scroll.svg") center center/cover;
  z-index: 10;
  color: #615349;
  cursor: default;
  opacity: 0;
}
.scroll-name p {
  display: inline-block;
  margin: 4px 0 0 15px;
}

.footstep {
  position: absolute;
  background: #615349;
  width: 6px;
  height: 12px;
  border-radius: 80% 80% 70% 70%/130% 130% 25% 25%;
  z-index: 10;
  opacity: 0;
}
.footstep:before {
  content: "";
  position: absolute;
  border-radius: 6px;
  width: 5px;
  height: 5px;
  top: 110%;
  left: 0px;
  background: #615349;
  border-radius: 0 0 100% 100%;
}
.footstep.left {
  -webkit-transform: rotate(5deg);
          transform: rotate(5deg);
}
.footstep.right {
  -webkit-transform: rotate(-3deg) translateY(15px) translateX(10px);
          transform: rotate(-3deg) translateY(15px) translateX(10px);
}

.footsteps-1 .footstep.left {
  bottom: 150px;
  left: 18px;
  -webkit-transform: rotate(35deg);
          transform: rotate(35deg);
}
.footsteps-1 .footstep.right {
  bottom: 150px;
  left: 28px;
  -webkit-transform: rotate(30deg);
          transform: rotate(30deg);
}

.footsteps-2 .footstep.left {
  bottom: 285px;
  left: 280px;
  -webkit-transform: rotate(-90deg);
          transform: rotate(-90deg);
}
.footsteps-2 .footstep.right {
  bottom: 275px;
  left: 285px;
  -webkit-transform: rotate(-85deg);
          transform: rotate(-85deg);
}
.footsteps-2 .scroll-name {
  bottom: 300px;
  left: 220px;
}

.instructions {
  text-align: center;
  color: #fff;
}
.instructions p {
  margin-bottom: 8px;
  line-height: 1.3;
}

.toggle-map {
  border: 2px solid;
  margin: 30px auto 0;
  background: transparent;
  font: 34px "Lobster Two", cursive;
  padding: 8px 30px;
  cursor: pointer;
  position: relative;
  color: #fff;
}
.toggle-map:before {
  content: "";
  position: absolute;
  width: calc(100% - 8px);
  height: 51px;
  left: 2px;
  top: 2px;
  border: 2px solid;
}
.toggle-map:hover:before {
  width: 100%;
  height: 100%;
  transition: 0.3s ease;
  left: -2px;
  top: -2px;
  right: auto;
}
.toggle-map:focus {
  outline: none;
}

.map-base.active .flap--1 {
  -webkit-transform: rotateX(180deg);
          transform: rotateX(180deg);
  -webkit-transform-origin: top center;
          transform-origin: top center;
  transition: 0.6s -webkit-transform 1.5s;
  transition: 0.6s transform 1.5s;
  transition: 0.6s transform 1.5s, 0.6s -webkit-transform 1.5s;
}
.map-base.active .flap--2 {
  -webkit-transform: rotateX(180deg);
          transform: rotateX(180deg);
  -webkit-transform-origin: bottom center;
          transform-origin: bottom center;
  transition: 0.6s -webkit-transform 1.8s;
  transition: 0.6s transform 1.8s;
  transition: 0.6s transform 1.8s, 0.6s -webkit-transform 1.8s;
}
.map-base.active .side-1 {
  -webkit-transform-origin: center left;
          transform-origin: center left;
  -webkit-transform: rotateY(180deg) skewY(2deg);
          transform: rotateY(180deg) skewY(2deg);
  transition: 0.5s all ease-in-out 0.6s;
}
.map-base.active .side-1 .front {
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}
.map-base.active .side-2 {
  -webkit-transform: rotateY(180deg) skewY(-2deg);
          transform: rotateY(180deg) skewY(-2deg);
  -webkit-transform-origin: center right;
          transform-origin: center right;
  transition: 0.5s all ease-in-out 0.6s;
}
.map-base.active .side-2 .front {
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}
.map-base.active .side-3 {
  left: -50%;
  -webkit-transform: skewY(2deg) translateX(-100%);
          transform: skewY(2deg) translateX(-100%);
  top: 8px;
  transition: 0.3s left ease 0.8s, 0.5s top ease 0.8s, 0.5s -webkit-transform ease 0.8s;
  transition: 0.5s transform ease 0.8s, 0.3s left ease 0.8s, 0.5s top ease 0.8s;
  transition: 0.5s transform ease 0.8s, 0.3s left ease 0.8s, 0.5s top ease 0.8s, 0.5s -webkit-transform ease 0.8s;
}
.map-base.active .side-4 {
  left: 100%;
  -webkit-transform: skewY(-2deg) translateX(100%);
          transform: skewY(-2deg) translateX(100%);
  top: 8px;
  margin-left: -7px;
  transition: 0.3s left ease 0.8s, 0.5s top ease 0.8s,
    0.5s margin ease 0.8s, 0.5s -webkit-transform ease 0.8s;
  transition: 0.5s transform ease 0.8s, 0.3s left ease 0.8s, 0.5s top ease 0.8s,
    0.5s margin ease 0.8s;
  transition: 0.5s transform ease 0.8s, 0.3s left ease 0.8s, 0.5s top ease 0.8s,
    0.5s margin ease 0.8s, 0.5s -webkit-transform ease 0.8s;
}
.map-base.active .side-5 {
  left: -100%;
  -webkit-transform-origin: center left;
          transform-origin: center left;
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
  transition: 0.7s left 0.8s, 0.2s margin 0.8s, 0.5s -webkit-transform;
  transition: 0.5s transform, 0.7s left 0.8s, 0.2s margin 0.8s;
  transition: 0.5s transform, 0.7s left 0.8s, 0.2s margin 0.8s, 0.5s -webkit-transform;
  top: 0px;
  margin-left: 4px;
}
.map-base.active .side-5 .front {
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
  transition: 0.1s -webkit-transform;
  transition: 0.1s transform;
  transition: 0.1s transform, 0.1s -webkit-transform;
}
.map-base.active .side-6 {
  left: 150%;
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
  -webkit-transform-origin: center right;
          transform-origin: center right;
  margin-left: -8px;
  transition: 0.7s left 0.8s, 0.5s top 0.8s,
    0.5s margin 0.8s, 0.5s -webkit-transform 0.3s;
  transition: 0.5s transform 0.3s, 0.7s left 0.8s, 0.5s top 0.8s,
    0.5s margin 0.8s;
  transition: 0.5s transform 0.3s, 0.7s left 0.8s, 0.5s top 0.8s,
    0.5s margin 0.8s, 0.5s -webkit-transform 0.3s;
}
.map-base.active .side-6 .front {
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
  transition: 0.1s -webkit-transform;
  transition: 0.1s transform;
  transition: 0.1s transform, 0.1s -webkit-transform;
}
.map-base.active .footstep,
.map-base.active .scroll-name {
  opacity: 1;
  transition: 0.5s opacity 2.5s;
}
.map-base.active .footsteps-1 .footstep {
  -webkit-animation: 15s footsteps-1 ease 3s forwards;
          animation: 15s footsteps-1 ease 3s forwards;
}
.map-base.active .footsteps-1 .scroll-name {
  -webkit-animation: 15s scroll-1 ease 3s forwards;
          animation: 15s scroll-1 ease 3s forwards;
}
.map-base.active .footsteps-2 .footstep {
  -webkit-animation: 15s footsteps-2 ease 3.2s forwards;
          animation: 15s footsteps-2 ease 3.2s forwards;
}
.map-base.active .footsteps-2 .scroll-name {
  -webkit-animation: 15s scroll-2 ease 3.2s forwards;
          animation: 15s scroll-2 ease 3.2s forwards;
}

