@-webkit-keyframes footsteps-1 {
  10% {
    -webkit-transform: translate(8px, -15px) rotate(30deg);
            transform: translate(8px, -15px) rotate(30deg);
  }
  20% {
    -webkit-transform: translate(30px, -45px) rotate(30deg);
            transform: translate(30px, -45px) rotate(30deg);
  }
  30% {
    -webkit-transform: translate(40px, -75px) rotate(20deg);
            transform: translate(40px, -75px) rotate(20deg);
  }
  40% {
    -webkit-transform: translate(45px, -100px) rotate(10deg);
            transform: translate(45px, -100px) rotate(10deg);
  }
  50% {
    -webkit-transform: translate(50px, -125px) rotate(10deg);
            transform: translate(50px, -125px) rotate(10deg);
  }
  60% {
    -webkit-transform: translate(50px, -135px) rotate(10deg);
            transform: translate(50px, -135px) rotate(10deg);
  }
  100% {
    -webkit-transform: translate(50px, -135px) rotate(20deg);
            transform: translate(50px, -135px) rotate(20deg);
  }
}

@keyframes footsteps-1 {
  10% {
    -webkit-transform: translate(8px, -15px) rotate(30deg);
            transform: translate(8px, -15px) rotate(30deg);
  }
  20% {
    -webkit-transform: translate(30px, -45px) rotate(30deg);
            transform: translate(30px, -45px) rotate(30deg);
  }
  30% {
    -webkit-transform: translate(40px, -75px) rotate(20deg);
            transform: translate(40px, -75px) rotate(20deg);
  }
  40% {
    -webkit-transform: translate(45px, -100px) rotate(10deg);
            transform: translate(45px, -100px) rotate(10deg);
  }
  50% {
    -webkit-transform: translate(50px, -125px) rotate(10deg);
            transform: translate(50px, -125px) rotate(10deg);
  }
  60% {
    -webkit-transform: translate(50px, -135px) rotate(10deg);
            transform: translate(50px, -135px) rotate(10deg);
  }
  100% {
    -webkit-transform: translate(50px, -135px) rotate(20deg);
            transform: translate(50px, -135px) rotate(20deg);
  }
}
